*:focus {
    outline: none;
}

html {
    scroll-snap-type: y mandatory;
    scroll-behavior: smooth;
    overflow: scroll;
}

body {
    background: #030305;
    cursor: none;
    height: 100%;
    width: 100%;
    margin: 0;
}

h1,
h2,
h3 {
    font-family: 'Lato', sans-serif;
    color: #fff;
}

h1 {
    font-size: 2.5rem;
}

p {
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 1px;
    font-size: 1rem;
    color: #fff;
    margin: 0;
}

a {
    color: #636e72;
    text-decoration: none;
}

a:hover {
    color: #ccc;
}

.cursor {
    width: 40px;
    height: 40px;
    border: 2px solid #fefefe;
    border-radius: 100%;
    position: fixed;
    transform: translate(-50%, -50%);
    pointer-events: none;
    transition: all 150ms ease;
    transition-property: background-color, opacity, transform, mix-blend-mode;
    z-index: 9999;
    mix-blend-mode: difference;
}

.cursor--hidden {
    opacity: 0;
}

.cursor--link-hovered {
    transform: translate(-50%, -50%) scale(1.25);
    background-color: #fefefe;
}

.cursor--clicked {
    transform: translate(-50%, -50%) scale(0.9);
    background-color: #fefefe;
}

header {
    background: url('./assets/Background.jpg');
    height: 100vh;
    width: 100%;
    display: flex;
    position: relative;
    align-items: center;
    scroll-snap-align: start;
}

@media (max-width: 992px) {
    header {
        background-position: top;
    }
}

header h1 {
    transform: translate(-50%, 0);
    text-transform: uppercase;
    position: absolute;
    letter-spacing: 4px;
    font-size: 4rem;
    z-index: 1;
    left: 50%;
}

header h1 span {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #fff;
    position: absolute;
    font-size: 10rem;

    bottom: -56px;
    right: -32px;
}

@media (max-width: 576px) {
    header h1 {
        font-size: 2.5rem;
    }
    header h1 span {
        font-size: 8.5rem;
    }
}

header #scroll {
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 5px;
    animation-delay: 5s;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    transform-origin: bottom;
    animation-name: scroll-bounce;
}

@keyframes scroll-bounce {
    0% {
        transform: scale(1, 1) translateY(0);
    }
    10% {
        transform: scale(1, 0.9) translateY(0);
    }
    30% {
        transform: scale(1, 1.1) translateY(-20px);
    }
    50% {
        transform: scale(1, 0.95) translateY(0);
    }
    57% {
        transform: scale(1, 1) translateY(-7px);
    }
    64% {
        transform: scale(1, 1) translateY(0);
    }
    100% {
        transform: scale(1, 1) translateY(0);
    }
}

main h2 {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 2.5rem;
}

@media (max-width: 992px) {
    main h2 {
        font-size: 2rem;
        margin: 0.8rem 0;
    }
    main p {
        font-size: 0.9rem;
    }
}

main .about {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    scroll-snap-align: start;
}

@media (max-width: 992px) {
    main .about {
        margin: 5rem 0;
        padding: 0 1rem;
        flex-direction: column;
    }
}

main .about .image {
    background: #131313;
}

main .about .image img {
    padding-left: 15px;
    padding-top: 15px;
    margin-right: -10px;
    margin-bottom: -10px;
}

@media (max-width: 992px) {
    main .about .image {
        background: none;
    }
    main .about .image img {
        width: 100%;
        margin: 0;
        padding: 0;
    }
}

main .about .info {
    margin-left: 2.5rem;
    width: 35%;
}

@media (max-width: 992px) {
    main .about .info {
        width: 100%;
        padding: 0 1rem;
        margin-left: 0;
    }
}

main .info ion-icon {
    margin-top: 2.5rem;
    margin-right: 0.5rem;
    color: #fff;
}

main .portfolio {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    scroll-snap-align: start;
}

@media (max-width: 992px) {
    main .portfolio {
        grid-template-columns: 1fr 1fr;
    }
}

main .portfolio h2 {
    text-transform: uppercase;
}

@media (max-width: 992px) {
    main .portfolio h2 {
        padding: 0 10px;
    }
}

main .portfolio span {
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-color: #fff;
    -webkit-text-stroke-width: 2px;
}

main .portfolio .item {
    position: relative;
    height: 100vh;
}

@media (max-width: 576px) {
    main .portfolio .item {
        height: 50vh;
    }
}

main .portfolio .item img {
    height: 100%;
    width: 100%;
}

main .portfolio .item:first-child img {
    border-right: 3px solid #fff;
}

main .portfolio .item:last-child img {
    border-left: 3px solid #fff;
}

@media (max-width: 992px) {
    main .portfolio .item:first-child img,
    main .portfolio .item:last-child img {
        border: 0;
    }
    main .portfolio .item:last-child img {
        display: none;
    }
}

@media (max-width: 576px) {
    main .portfolio .item:last-child img {
        display: inline;
    }
}

main .portfolio .item-special {
    flex-direction: column;
    text-align: center;
    display: flex;
    width: 100%;
}

main .portfolio .item-special img {
    width: 100%;
}

main .portfolio .item-special h2 {
    margin-top: auto;
    margin-bottom: auto;
}

main .portfolio .special-mobile {
    display: none;
}

@media (max-width: 576px) {
    main .portfolio .special-mobile {
        border-top: 3px solid #fff;
        display: flex;
    }
    main .portfolio .item:last-child {
        border-top: 3px solid #fff;
    }
}

#contact {
    scroll-snap-align: start;
    height: 60vh;
    width: 100%;
}

.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 3rem;
    margin-top: 4rem;
}

.contact h2 {
    margin-bottom: 0.2rem;
}

.contact .options {
    justify-content: center;
    margin-top: 4rem;
    display: flex;
    gap: 4rem;
}

.contact .options .item {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.contact .options ion-icon {
    margin-bottom: 0.5rem;
    font-size: 2rem;
    color: #bdc3c7;
}

.contact .options p {
    color: #bdc3c7;
    letter-spacing: 0.5px;
}

footer {
    justify-content: space-around;
    align-items: center;
    background: #131313;
    padding: 2rem 0;
    display: flex;
    height: 100%;
}

@media (max-width: 992px) {
    footer {
        flex-direction: column;
    }
}

footer h3 {
    text-transform: uppercase;
}

footer ul {
    list-style: none;
    padding: 0;
}

footer li {
    margin: 0.8rem 0;
}

footer a {
    color: #fff;
}

footer .info {
    display: flex;
    gap: 8rem;
}

.copyright {
    height: 8vh;
}

.copyright {
    display: flex;
    align-items: center;
    justify-content: center;
}

.copyright p {
    font-size: 0.9rem;
    color: #ccc;
}
